<script>
import Vue from "vue";
import {Lazyload} from 'vant';
import {HTMLDecode, getDateDiff} from "@/utils/other/commonFun"
import {codeProductTop, codeArticleProduct} from "@/api/codeProducts";

Vue.use(Lazyload);
export default {
  name: "Article",
  data() {
    return {
      sid: null,
      article: [],
      articleTopList: [],
      clientWidth: document.body.clientWidth
    }
  },
  mounted() {
    this.article = this.$route.params;
    this.getCodeProductTopList()
    window.onresize = () => {
      this.clientWidth = document.body.clientWidth
    }
  },
  watch: {
    "$route.query": {
      immediate: true,
      // deep:true,
      handler(newValue) {
        if (newValue.sid) {
          this.getCodeArticleProduct(newValue.sid)
        } else {
          this.article = this.$route.params;
        }
      }
    }
  },
  methods: {
    getCodeProductTopList(page = null) {
      if (!page) page = this.page
      let that = this
      codeProductTop({cate_id: this.$route.params.id, limit: 4}).then(res => {
        that.articleTopList = res.list
      })
    },
    getCodeArticleProduct(sid) {
      let that = this
      codeArticleProduct({product_id: sid}).then(res => {
        that.$nextTick(() => {
          that.article = res
        })
      })
    },
    HTMLDecode(str) {
      return HTMLDecode(str)
    },
    getDateDiff(str) {
      return getDateDiff(str)
    },
  }
}
</script>

